@font-face {
    font-family: without;
    src: url(/fonts/WithoutAltSans-Black.ttf);
}



body{
    margin: 0;
    padding: 0;
    background-color: beige;
    color: wheat;
}

//css for the photo shuffle
.photoshuffle{
    width: 100vw;
    background-image: url(/static/images/4.png);
    background-size: cover; /* Ensure the image covers the entire div */
    background-position: center; /* Center the image */
    background-repeat: no-repeat; /* Ensure the image covers the entire div */
    height: 80vh;
}

nav{
    display: flex;
    flex-direction: row;
    width: 100vw;
    align-items: center;
    justify-content: space-around;
}

nav .links{
    display: flex;
    width: 50vw;
}

.links a{
    margin-left: 4vw;
    font-family: Verdana, Geneva, Tahoma, sans-serif;
    font-size: small;
    text-decoration: none;
    color: wheat;
    transition: color 1s ease;
}

.links a:hover{
    color: gold;
}


nav .logo{
    font-family: without;
    display: flex;
    align-items: center;
}

nav .logo ion-icon{
  margin-left: 2vw;
}